 nav{
    background: rgba(0, 0, 0, 0.3);
    width:max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translate(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
 }

 nav a{
    background-color: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
    
 }

 nav a:hover{
    background: rgba(0, 0, 0, 0.2);
 }

 nav a.active{
    background-color: var(--color-bg);
    color: var(--color-white);
 }

 @media screen and (max-width:320px){
   nav{
      width: 80%;
   }
   nav a{
      background-color: transparent;
      padding: 0.7rem;
      border-radius: 50%;
      display: flex;
      color: var(--color-light);
      font-size: 0.7rem;
      
   }
 }

 @media screen and (min-width:374px) and (max-width:425px){
   
   nav a{
      background-color: transparent;
      padding: 0.9rem;
      border-radius: 50%;
      display: flex;
      color: var(--color-light);
      font-size: 1rem;
      
   }
 }